<template>
  <div class="login">
    <div class="goto" v-show="gotoshow">
      <input type="text" placeholder="输入口令" v-model="pass" maxlength="30" @keyup.enter="login">
      <button @click="login()">登录</button>
    </div>
    <!-- 显示解析的界面 -->
    <div class="down" v-show="down">
      <h1>剩余下载次数: <span v-text="num"></span> </h1>
      <p class="input"><input type="text" v-model="urlId" maxlength="5" placeholder="请输入要下载的ID,按回车键下载！" @keyup.enter="downfn()"></p>
      <div class="tps">
          <h2>使用说明</h2>
          <p>1.选择要下载的模板简历 http://www.500d.me/template</p>
          <p>2.如要下载的模板地址是 http://www.500d.me/template/924.html <br/>924就是ID号码</p>
          <p>3.把ID号码输入文本框</p>
          <p class="warning">请合理使用!</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      pass: '',
      urlId: '',
      num: 0,
      gotoshow: false,
      down: false
    }
  },
  mounted () {
    this.isLogin()
  },
  methods: {
    login () {
      if (this.pass === '') {
        return false
      } else {
        this.$ajax.post('/api/login', {
          pass: this.pass
        }).then((res) => {
          if (res.data.status === 1) {
            this.gotoshow = false
            this.down = true
            this.myData = res.data.result[0].num
          }
        })
      }
    },
    downfn () {
      if (this.urlId === '') {
        return false
      } else {
        this.$ajax.post('/api/down', {
          id: this.urlId
        }).then(res => {
          if (res.data.status === 1) {
            this.num = this.num - 1
            window.open(res.data.result.content)
          } else {
            alert(res.data.msg)
          }
        })
      }
    },
    isLogin () {
      this.$ajax.post('/api/islogin').then(res => {
        if (res.data.status === 1) {
          this.gotoshow = false
          this.down = true
          this.num = res.data.result[0].num
        } else {
          this.gotoshow = true
          this.down = false
        }
      })
    }
  }
}
</script>

<style lang="stylus">
.login
  width 100%
  height 100%
  background url('./loginBg.jpg') no-repeat 100% 100%
  position relative
  >.goto
    width 400px
    height 200px
    border-radius 10px
    position absolute
    left 50%
    top 50%
    transform translate(-50%,-100%)
    background rgba(255,255,255,.3)
    >input
      display block
      width 250px
      height 30px
      border none
      outline none
      padding-left 10px
      border-radius 4px
      background rgba(0,0,0,.1)
      margin 40px auto
      color #fff
    >button
      display block
      width 100px
      height 40px
      background rgba(199,122,58,.6)
      border-radius 10px
      border none
      outline none
      cursor pointer
      color #fff
      margin 0 auto
  >.down
    width 600px
    height 400px
    border-radius 10px
    position absolute
    left 50%
    top 50%
    transform translate(-50%,-70%)
    background rgba(255,255,255,.3)
    >h1
      width 100%
      height 80px
      line-height 80px
      text-align center
      color #000
      >span
        color #333
    >.input
      width 400px
      height 40px
      margin 0 auto
      >input
        width 100%
        height 100%
        border none
        padding-left 10px
        box-sizing border-box
        outline none
        border-radius 4px
    >.tps
      width 100%
      height 280px
      box-sizing border-box
      padding 40px
      padding-top 60px
      position relative
      >p
        line-height 24px
      >.warning
        position absolute
        left 0
        bottom 0
        width 100%
        height 30px
        text-align right
        box-sizing border-box
        padding 0 20px
        color #333
</style>



